<style lang="stylus" scoped>
  .boxlist
    width 10.04rem
    cursor pointer
  .over
    opacity 0.5
  .sendname
    font-size 12px
    margin-bottom 0.32rem
    .time
      color: #cccccc;
  .box
    width 100%
    height: 3.7rem;
    border-radius: 0.48rem;
    border: solid 0.04rem #dedede;
    overflow hidden
    margin-bottom 0.98rem
    .box-top
      height: 2.58rem;
      background #f99c3b
      align-items center
      img 
        width: 1.54rem;
        height: 1.9rem;
        margin 0 0.44rem 0 0.48rem;
      p:first-of-type
        color: #ffffff;
        font-size: 0.6rem;
        margin-bottom 0.28rem
      p:last-of-type
        color: #ffffff;
        font-size: 0.48rem;
    .box-bottom
      height 1.12rem
      line-height 1.12rem
      margin-left 0.48rem
      font-size 0.48rem
</style>

<template>
  <div class="boxlist" :class="item.none?'over':''" @click="go">
    <p class="sendname">{{item.name}} <span class="time">( {{timer}} )</span></p>
    <div class="box">
      <div class="box-top flex">
        <img src="../common/images/bao.png">
        <div>
          <p>{{thislang.baotxt}}</p>
          <p>{{thislang.baotxt1}}</p>
        </div>
      </div>
      <div class="box-bottom">{{thislang.baotxt2}}：{{item.num}}</div>
    </div>
  </div>
</template>

<script>
import {mapGetters,mapMutations} from 'vuex'
import { date } from 'quasar'
import {selectPacket,getjin} from '../scattereos'
import {post} from '../api'
export default {
  props:{
    item:{
      type:Object
    },
    index:{
      type:Number
    }
  },
  data(){
    return{
      over:true
    }
  },
  methods:{
    go(){
      // 判断登录
      if(this.infos.name.length == 0){
        alert('请先登录')
        return false
      }
      // 判断是否领完或已领取
      if(this.item.none || Number(this.item.isgo) == 1){
        this.$router.push(`/record-this/${Number(this.item.packetId)}`)
        return false
      }
      this.$q.loading.show()
      selectPacket(this.infos.name,Number(this.item.packetId),"qizhan").then((val)=>{
        console.log(val)
        getjin(this.infos.name)
        let item = {}
        if(val.isLast == '1'){
          item = {
            index:this.index,
            data:{
              none:true,
              isgo:1
            }
          }
        }else{
          item = {
            index:this.index,
            data:{
              isgo:1
            }
          }
        }
        this.setpackdata(item)
        this.$q.loading.hide()
        let data = {
          token:this.$q.sessionStorage.get.item('token'),
          userid:this.$q.sessionStorage.get.item('userid'),
          outid:val.packetId,
          eosid:val.packetId,
          blocknumber:val.block_num,
          income_sum:(val.packetAmount/10000).toFixed(4),
          is_chailei:val.isBomb,
          reward_type:val.isLuck,
          reward_sum:(val.luckyAmount/10000).toFixed(4),
          addr:this.infos.B_name,
          isnone:val.isLast,
          isgo:1
        }
        let win = {
          print:(val.packetAmount/10000).toFixed(4),
          is_chailei:val.isBomb,
          reward:val.isLuck,
          rewardsum:(val.packetAmount/10000).toFixed(4),
          num:this.item.num,
          eos:this.item.eos
        }
        this.$emit('myshow',win)
        post('/income_packet',data).then((val)=>{
          console.log(val)
        }) 
      }).catch(e => {
        this.$q.loading.hide()
        console.log(e)
      });
    },
    ...mapMutations({
      setpackdata:'SET_PACKDATA',
    }),
  },
  computed:{
    ...mapGetters([
      "thislang","infos"
    ]),
    timer(){
      return date.formatDate(this.item.time, 'HH:mm:ss')
    }
  },
}
</script>
