<style lang="stylus" scoped>
  .recordHair
    max-width 16rem
    margin  0 auto
  .top
    background #f0f0f0 url("../common/images/bg3.jpg") no-repeat top
    background-size 100% auto
    min-height 10.82rem
    padding-top 5.78rem
    text-align center
    color #222222
    .name
      font-size 0.72rem
    .allprice
      font-size 1.2rem
      margin-top 0.7rem
      font-weight bold
      span 
        font-size 0.96rem
    .tablebox
      margin-top 0.7rem
      li
        flex 0 0 25%
        margin 0.4rem 0
        p
          color #b2b2b2
          &:first-of-type
            font-size 0.72rem
          &:last-of-type
            font-size 0.48rem  
    .time
      color #d87307
      font-size 0.56rem
      position absolute
      right 0.8rem
      top 4.6rem
      cursor pointer
  .bottom
    border-top 0.04rem solid #e8e8e8
    background #ffffff
    li
      border-bottom 0.04rem solid #e8e8e8
      padding 0.68rem 0.8rem
      .info
        justify-content space-between
      .name,.price
        font-size 14px
        color #222222
      .time,.num1
        font-size 0.48rem
        color #999999
        margin-top 0.16rem
      .num1
        text-align right
</style>

<template>
  <div class="recordHair fullscreen scroll">
    <record-head class="fixed-top" :title="`EOS ${thislang.shou}`"></record-head>
    <div class="top">
      <p class="name">{{data.name}}{{thislang.gohuo}}<br />{{data.packetcount}}{{thislang.ge}}</p>
      <p class="allprice">{{data.packetsum}}EOS</p>
      <ul class="flex tablebox">
        <li>
          <p>-</p>
          <p>{{thislang.big}}</p>
        </li>
        <li>
          <p>{{data.paris}}</p>
          <p>{{thislang.dui}}</p>
        </li>
        <li>
          <p>{{data.three}}</p>
          <p>{{thislang.san}}</p>
        </li>
        <li>
          <p>-</p>
          <p>{{thislang.small}}</p>
        </li>
        <li>
          <p>{{data.int}}</p>
          <p>{{thislang.zhen}}</p>
        </li>
        <li>
          <p>{{data.shunzi}}</p>
          <p>{{thislang.shun}}</p>
        </li>
        <li>
          <p>{{data.bomb}}</p>
          <p>{{thislang.zha}}</p>
        </li>
        <li>
          <p>{{data.chailei}}</p>
          <p>{{thislang.steptitle}}</p>
        </li>
      </ul>
      <span class="time" @click="timer = !timer">{{thisdata}}</span>
    </div>
    <ul class="bottom">
      <li :key="index" v-for="(item,index) in data.data">
        <div class="info flex">
          <div class="left">
            <p class="name">{{item.outpacket_sum}}EOS</p>
            <p class="time">{{item.created_at}}</p>
          </div>
          <div class="right">
            <p class="price">{{item.income_sum}}EOS</p>
            <p class="num1"></p>
          </div>
        </div>
      </li>
    </ul>
    <datetime v-show="timer" @newtime="newtime" :timej="timej"></datetime>
  </div>
</template>

<script>
import recordHead from '@/components/recordHead.vue'
import datetime from '@/components/datetime.vue'
import { date } from 'quasar'
import {mapGetters} from 'vuex';
import {post} from '../api'
export default {
  created(){
    if(this.infos.name.length == 0){
      alert('请先登录')
      this.$router.push('/')
      return false
    }
    this.getinfo()
  },
  data(){
    return{
      model:new Date(),//时间
      timer:false, //时间选择展示
      data:{},
      timej:{}
    }
  },
  components: {
    "record-head":recordHead,
    datetime
  },
  methods:{
    // 获取新时间
    newtime(newtime){
      if(newtime){
        let time = date.formatDate(newtime, 'X')
        console.log(time)
        this.getinfo(time)
        this.model = newtime
      }
      this.timer = false
    },
    getinfo(time){
      let data = {}
      if(time){
        data = {
          token:this.$q.sessionStorage.get.item('token'),
          userid:this.$q.sessionStorage.get.item('userid'),
          time:time
        }
      }else{
        data = {
          token:this.$q.sessionStorage.get.item('token'),
          userid:this.$q.sessionStorage.get.item('userid')
        }
      }
      post('/my_income_packet',data).then((val)=>{
        console.log(val)
        this.data = val
        this.timej = {
          first:val.last_time*1000,
          last:val.max_time*1000
        }
        console.log(this.timej)
      })
    }
  },
  computed:{
    // 时间转换
    thisdata(){
      return  `${date.formatDate(this.model, 'MM')}月${date.formatDate(this.model, 'DD')}日`
    },
    ...mapGetters([
      "thislang","infos"
    ]),
  }
}
</script>

