<style lang="stylus" scoped>
  .recordHair
    max-width 16rem
    margin  0 auto
  .fixed-top
    z-index 999
  .top
    background url("../common/images/bg3.jpg") no-repeat top
    background-size 100% 100%
    height 10.82rem
    padding-top 5.78rem
    text-align center
    color #222222
    position relative
    z-index 1
    p
      font-size 0.64rem
    h3
      font-size 30px
      font-weight bold
    .time
      color #d87307
      font-size 0.56rem
      cursor pointer
  .bottom
    background #ffffff
    .title
      height: 1.66rem;
      background #c7412f
      align-items center
      font-size 0.6rem
      color #ffffff
      span 
        flex 1
        text-align center
    .tr
      height: 2.5rem;
      font-size 0.56rem
      align-items center
      background #f8f8f8
      margin-bottom 0.04rem
      span 
        flex 1
        text-align center
</style>

<template>
  <div class="recordHair fullscreen scroll" :class="$q.platform.is.desktop?'desktop':''">
    <record-head class="fixed-top" :class="$q.platform.is.desktop?'desktop':''" :title="`EOS 领奖记录`" :jiang="false"></record-head>
    <div class="top">
      <p>奖励记录</p>
      <h3>108.0986EOS</h3>
      <span class="time" @click="timer = !timer">{{thisdata}}</span>
    </div>
    <ul class="bottom">
      <li class="title flex">
        <span>时间</span>
        <span>数量</span>
      </li>
      <li class="tr flex" :key="index" v-for="(item,index) in 6">
        <span>07:00:00</span>
        <span>4421.1875 EOS</span>
      </li>
    </ul>
    <datetime v-show="timer" @newtime="newtime"></datetime>
  </div>
</template>

<script>
import recordHead from '@/components/recordHead.vue'
import datetime from '@/components/datetime.vue'
import { date } from 'quasar'
import {mapGetters} from 'vuex';
export default {
  data(){
    return{
      model:new Date(),//时间
      timer:false//时间选择展示
    }
  },
  components: {
    "record-head":recordHead,
    datetime
  },
  methods:{
     // 获取新时间
    newtime(newtime){
      if(newtime){
        this.model = newtime
      }
      this.timer = false
    }
  },
  computed:{
    // 时间转换
    thisdata(){
      return  `${date.formatDate(this.model, 'MM')}月${date.formatDate(this.model, 'DD')}日`
    },
    ...mapGetters([
      "thislang"
    ])
  }
}
</script>

